import {FocusMonitor, FocusableOption} from '@angular/cdk/a11y';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  ViewEncapsulation,
  TemplateRef,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { OrderStepLabel } from './step-label';
import { MatStepperIntl } from './stepper-intl';
import { OrderStepperIconContext } from './stepper-icon';
import { StepState } from '@angular/cdk/stepper';


@Component({
  moduleId: module.id,
  selector: 'order-step-header',
  templateUrl: 'step-header.html',
  styleUrls: ['step-header.scss'],
  host: {
    class: 'order-step-header',
    role: 'tab',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class OrderStepHeader implements FocusableOption, OnDestroy {
  private _intlSubscription: Subscription;

  /** State of the given step. */
  @Input() state: StepState;

  /** Label of the given step. */
  @Input() label: OrderStepLabel | string;

  /** Error message to display when there's an error. */
  @Input() errorMessage: string;

  /** Overrides for the header icons, passed in via the stepper. */
  @Input() iconOverrides: {[key: string]: TemplateRef<OrderStepperIconContext>};

  /** Index of the given step. */
  @Input() index: number;

  /** Whether the given step is selected. */
  @Input() selected: boolean;

  /** Whether the given step label is active. */
  @Input() active: boolean;

  /** Whether the given step is optional. */
  @Input() optional: boolean;

  constructor(
    public _intl: MatStepperIntl,
    private _focusMonitor: FocusMonitor,
    protected _elementRef: ElementRef<HTMLElement>,
    changeDetectorRef: ChangeDetectorRef) {
    _focusMonitor.monitor(_elementRef, true);
    this._intlSubscription = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  ngOnDestroy() {
    this._intlSubscription.unsubscribe();
    this._focusMonitor.stopMonitoring(this._elementRef);
  }

  focus() {
    this._elementRef.nativeElement.focus();
  }
  
  /** Returns string label of given step if it is a text label. */
  _stringLabel(): string | null {
    return this.label instanceof OrderStepLabel ? null : this.label;
  }

  /** Returns MatStepLabel if the label of given step is a template label. */
  _templateLabel(): OrderStepLabel | null {
    return this.label instanceof OrderStepLabel ? this.label : null;
  }

  /** Returns the host HTML element. */
  _getHostElement() {
    return this._elementRef.nativeElement;
  }

  /** Template context variables that are exposed to the `matStepperIcon` instances. */
  _getIconContext(): OrderStepperIconContext {
    return {
      index: this.index,
      active: this.active,
      optional: this.optional
    };
  }
}
