import {Directive} from '@angular/core';
import {CdkStepper, CdkStepperNext, CdkStepperPrevious} from '@angular/cdk/stepper';
import {OrderStepper} from './stepper';

/** Button that moves to the next step in a stepper workflow. */
@Directive({
  selector: 'button[orderStepperNext]',
  host: {
    '(click)': '_stepper.next()',
    '[type]': 'type',
  },
  inputs: ['type'],
  providers: [{provide: CdkStepper, useExisting: OrderStepper}]
})
export class OrderStepperNext extends CdkStepperNext {}

/** Button that moves to the previous step in a stepper workflow. */
@Directive({
  selector: 'button[orderStepperPrevious]',
  host: {
    '(click)': '_stepper.previous()',
    '[type]': 'type',
  },
  inputs: ['type'],
  providers: [{provide: CdkStepper, useExisting: OrderStepper}]
})
export class OrderStepperPrevious extends CdkStepperPrevious {}

// TODO(devversion): workaround for https://github.com/angular/material2/issues/12760
(OrderStepperNext as any)['ctorParameters'] = () => (CdkStepperNext as any)['ctorParameters']; //tslint:disable-line
(OrderStepperPrevious as any)['ctorParameters'] = () => //tslint:disable-line
    (CdkStepperPrevious as any)['ctorParameters']; //tslint:disable-line
